.Dashboard {
	background-color: #F5F7FB;
	padding: 32px 24px;
	
	.title {
		font-family: Usual;
		font-weight: 700;
		font-size: 2rem;
		align-items: flex-start;
		color: #272E4C;
	}

	.sub-title {
		font-family: Usual;
		font-weight: 600;
		font-size: 1.25rem;
		color: #9EA3AC;
		margin-bottom: 16px;
	}

	.content {
		min-height: 200px;
		border-radius: 12px;
		margin-bottom: 16px;
		overflow: hidden;
		box-shadow: 0px 3px 6px #272E4C19;
	}

	.loading-indicator {
		color: green[500];
		justify-content: 'center';
        margin: auto;
        z-index: -1;
	}

	.card-header {
		height: 104px;
		background-color: #FAFCFE;
		padding: 16px;
	}

	.card-graph-header {
		padding: 16px 0px;
		margin: 0px 16px;
	}

	.card-content {
		min-height: 200px;
		padding: 0px 16px;
	}

	.card-footer {
		padding: 16px;
	}
	
	.dashboard-temp-graph-img-report {
		height: 240px;
		max-width: 100%;
		margin: 16px 0px;
	}

	.dashboard-temp-graph-img {
		width: 100%;
		margin: 16px 0px;
	}

	.dashboard-graph-title {
		font-family: Usual;
		font-weight: 600;
		font-size: 1.5rem;
		color: #151C1B;
	}

	.dashboard-count {
		font-size: 2.25rem;
	}

	.dashboard-count-description {
		font-family: Usual;
		font-weight: 600;
		font-size: 0.875rem;
		color: #151C1B;
	}

	.dashboard-table-hg-even-row{
		background-color: #FFF1F1;
	}

	.dashboard-table-even-row{
		background-color: #FFFFFF;
	}

	.dashboard-table-hg-odd-row{
		background-color: #F7EAEC;
	}

	.dashboard-table-odd-row{
		background-color: #FAFCFE;
	}

	.dashboard-table-header-cell {
		font-family: Usual;
		font-weight: 700;
		font-size: 0.875rem;
		color: #3C394A;
	}

	.dashboard-table-data-cell {
		font-family: Usual;
		font-size: 0.875rem;
		color: #151C1B;
	}

	.dashboard-list-item-text {
		font-family: Usual;
		font-size: 0.875rem;
		color: #151C1B;
	}

	.dashboard-list-item-link {
		font-family: Usual;
		font-weight: 700;
		font-size: 0.875rem;
		color: #3AB77D;
		text-decoration: none;
	}

	.dashboard-view-more-link {
		font-family: Usual;
		font-weight: 700;
		font-size: 0.875rem;
		color: #3AB77D;
		text-decoration: none;
	}
}
